body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.game {
  font: 22px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  min-height: 260px;
  margin-top: 70px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.game {
  margin: 20px;
  min-height: 260px;
  margin-top: 70px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.game ol,
.game ul {
  padding-left: 30px;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}
.status {
  margin-bottom: 10px;
}
.restart {
  margin-top: 10px;
}
.square:focus {
  outline: none;
  background: #ddd;
}
.game-info {
  margin-left: 20px;
  min-width: 190px;
}

